.EditHero-container {
  background-color: $bgc2;

  .linear-container {
    background: linear-gradient(transparent 0, rgba(0, 0, 0, 0.5) 100%);
  }

  .img-edit-container {
    height: 200px;
    width: 200px;
    background-color: rgb(40, 40, 40);
    position: absolute;
    bottom: 90px;
    display: inline-block;
    box-shadow: 0 4px 60px rgb(0 0 0 / 50%);
    cursor: pointer;

    @include for-mobile-layout {
      left: 50%;
      transform: translateX(-50%);
    }

    @include for-narrow-layout {
      bottom: 40px;
    }

    @include for-wide-layout {
      height: 250px;
      width: 250px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center center;
    }

    .img-awesome {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      flex-wrap: wrap;
      justify-content: space-around;
      gap: 10px;
      p {
        font-weight: 900;
        text-align: center;
      }
    }

    .note {
      font-size: rem(80px);
    }

    .pencil {
      font-size: rem(50px);
    }
  }
  .info-container {
    cursor: pointer;
  }
}
